.App {
  text-align: center;
  position: absolute;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root {
  --color-bg1: rgb(108, 0, 162);
  --color-bg2: rgb(0, 17, 82);
  --color1: 18, 113, 255;
  --color2: 221, 74, 255;
  --color3: 100, 220, 255;
  --color4: 220, 200, 60;
  --color5: 180, 180, 50;
  --color-interactive: 140, 100, 255;
  --circle-size: 80%;
  --blending: hard-light;
}

@keyframes moveInCircle {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(100deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes moveVertical {
  0% {
    transform: translateY(-50%);
  }
  50% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(-50%);
  }
}

@keyframes moveHorizontal {
  0% {
    transform: translateX(-50%) translateY(-40%);
  }
  50% {
    transform: translateX(50%) translateY(40%);
  }
  100% {
    transform: translateX(-50%) translateY(-40%);
  }
}

.gradient-bg {
  width: 100%;
  height: 400%;
  position: absolute;
  overflow: hidden;
  background: linear-gradient(40deg, var(--color-bg1), var(--color-bg2));
  top: 0;
  left: 0;
}

.gradient-bg svg {
  display: none;
}

.gradient-bg .gradients-container {
  filter: url(assets/filter.svg#goo) blur(40px);
  width: 100%;
  height: 100%;
}

.gradient-bg .g1 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color1), 0.8) 0, rgba(var(--color1), 0) 50%) no-repeat;
  width: var(--circle-size);
  height: var(--circle-size);
  left: calc(50% - var(--circle-size) / 2);
  top: calc(50% - var(--circle-size) / 2);
  mix-blend-mode: var(--blending);
  transform-origin: center center;
  animation: moveVertical 33s ease infinite;
  opacity: 1;
}

.gradient-bg .g2 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color2), 0.8) 0, rgba(var(--color2), 0) 50%) no-repeat;
  width: var(--circle-size);
  height: var(--circle-size);
  left: calc(50% - var(--circle-size) / 2);
  top: calc(50% - var(--circle-size) / 2);
  mix-blend-mode: var(--blending);
  transform-origin: calc(70%) calc(50%);
  animation: moveInCircle 15s reverse infinite;
  opacity: 1;
}
.gradient-bg .g3 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color3), 0.8) 0, rgba(var(--color3), 0) 50%) no-repeat;
  width: var(--circle-size);
  height: var(--circle-size);
  left: calc(50% - var(--circle-size) / 2 - 500px);
  top: calc(50% - var(--circle-size) / 2 + 200px);
  mix-blend-mode: var(--blending);
  transform-origin: calc(70%);
  animation: moveInCircle 20s linear infinite;
  opacity: 1;
}
.gradient-bg .g4 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color4), 0.8) 0, rgba(var(--color4), 0) 50%) no-repeat;
  width: var(--circle-size);
  height: var(--circle-size);
  left: calc(50% - var(--circle-size) / 2);
  top: calc(50% - var(--circle-size) / 2);
  mix-blend-mode: var(--blending);
  transform-origin: calc(80%);
  animation: moveHorizontal 25s ease infinite;
  opacity: 0.7;
}
.gradient-bg .g5 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color5), 0.8) 0, rgba(var(--color5), 0) 50%) no-repeat;
  width: var(--circle-size) * 2;
  height: var(--circle-size) * 2;
  left: calc(50% - var(--circle-size) / 2);
  top: calc(50% - var(--circle-size) / 2);
  mix-blend-mode: var(--blending);
  transform-origin: calc(50% - 70%) calc(70%);
  animation: moveInCircle 30s ease infinite;
  opacity: 1;
}

.gradient-bg .interactive {
  position: absolute;
  background: radial-gradient(circle at center, rgba(var(--color-interactive), 0.8) 0, rgba(var(--color-interactive), 0) 50%) no-repeat;
  width: 100%;
  height: 100%;
  left: -50%;
  top: -50%;
  mix-blend-mode: var(--blending);
  opacity: 0.7;
}

