.App, .mobile, .phone, .phone-mirror, .topWrapper, .camera, .line-rec {
  animation: spin 5s linear infinite alternate;
  z-index: 1;
}

.App {
  position: absolute;
  left: 10%;
  top: 100%;
}

@keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(20deg) rotateX(10deg);
  }
}

.mobile {
     height: 100vh;
     display: flex;
     justify-content: center;
     align-items: center;
}
.phone {
     display: flex;
     justify-content: center;
     align-items: center;
     width: 331px;
     height: 616px;
     border-radius: 30px;
     padding: 10px;
}
.phone-mirror {
     position: relative;
     width: 328px;
     height: 600px;
     background-color: rgb(26, 25, 25);
     border-radius: 30px;
}
.topWrapper {
     position: relative;
     width: 130px;
     height: 20px;
     margin: 0px auto 0 auto;
     float: left;
     left: 30%;
     background-color: #232526;
     border-radius: 0 0 45px 45px;
}
.line-rec {
     position: relative;
     width: 60px;
     height: 6px;
     margin: 0px auto 0 auto;
     float: left;
     left: 20%;
     top: 4px;
     background-color: rgb(83, 83, 82);
     border-radius: 5px;
     border: 1px solid rgb(214, 214, 214);
}
.camera {
     position: relative;
     float: left;
     margin-right: 8px;
     left: 18%;
     width: 11px;
     height: 11px;
     top: 1px;
     background-color: rgb(83, 83, 82);
     border-radius: 30px;
     border: 1px solid rgb(214, 214, 214);
}
.buttons {
     background-color: black;
     width: 100%;
     height: 30px;
     border-radius: 0 0 45px 45px;
}
